.row{
    margin-left:20px;
}
.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_posters::-webkit-scrollbar{
    display: none;
}
.row_poster{
    object-fit: contain;
    width: 100%;
    max-height: 11rem !important;
    margin-right:10px;
    transition: transform 450ms
}

.row_poster:hover{
    transform: scale(1.08);
    opacity: 1;
}

.row_posterLarge{
    max-height: 20rem !important;
}

.row_posterLarge:hover{
    transform: scale(1.09);
}
.row_title{
    color: #fff;
}